/*global chrome*/
import Timer from '../services/Timer';
import Storage from '../services/Storage';
import Badge from '../services/Badge';

let currData = []
let pinnedData = [];
var allMyReg = /\b(?:raw|share|chinh-sach|lien-he|app)\b/gi;
var today = new Timer("H:m:s D/M/Y");
var storage = new Storage();
var _pinnedNote = "pinnedNoteList";
var _trackingNote = "noteTrackList";

chrome.webNavigation.onCompleted.addListener(function() {

  var trackList = storage.get(_trackingNote);
  var pinnedList = storage.get(_pinnedNote);
  
  if(!pinnedList) {
    storage.set(pinnedData, _pinnedNote);
  }
  
  if(!trackList) {
    storage.set(currData, _trackingNote)
  }

  chrome.tabs.query({ url: "*://*.notevn.com/*" }, function (tabs) {
    tabs.forEach(function(tab) {
      pinnedData = pinnedList;
      currData = trackList;
      var currUrl = new URL(tab.url).pathname.replace('/', '');
      if(
        !currData.some(noteUrl =>  noteUrl.url === currUrl ) && 
        !pinnedData.some(noteUrl => noteUrl.url === currUrl) &&
        !allMyReg.test(tab.url) && 
        !tab.url.includes("view-source:")
        ) {
          currData.unshift({
              url: currUrl,
              time: today.timeNow()
          });
          storage.set(currData, _trackingNote);
          Badge.updateText(pinnedData.length + currData.length);
          Badge.updateIcon(true);
      } else if(currData.some(noteUrl => noteUrl.url === currUrl)){
        for(var i = currData.length - 1; i >= 0; i--) {
            if(currData[i].url === currUrl) {
              currData.splice(i, 1);
            }
        }
        currData.unshift({
          url: currUrl,
          time: today.timeNow()
        });
        storage.set(currData, _trackingNote);
      }
    });
  });
}, {url: [{urlMatches : 'https://notevn.com/'}]});