/* global chrome:true */
const iconPaths = Object.freeze({
    16: 'img/icon-16.png',
    32: 'img/icon-48.png',
    48: 'img/icon-128.png',
});
  
const updatedIconPaths = Object.freeze({
    16: 'img/icon-16_updated.png',
    32: 'img/icon-48_updated.png',
    48: 'img/icon-128_updated.png',
});
  
  class Badge {
    static updateIcon(isRunning) {
      if (isRunning) {
        this.showUpdatedIcon();
      } else {
        this.showExtIcon();
      }
    }
  
    static showExtIcon() {
      chrome.browserAction.setIcon({
        path: iconPaths,
      });
    }
  
    static showUpdatedIcon() {
      chrome.browserAction.setIcon({
        path: updatedIconPaths,
      });
    }
  
    static updateText(numOfNote) {
      let text = String(numOfNote);
  
      if (numOfNote > 10) {
        text = '10+';
      } else if (numOfNote < 1) {
        text = '';
      }
  
      chrome.browserAction.setBadgeText({
        text,
      }, this.updateIcon(false));
    }
  }
  
  export default Badge;
  